'use client';
import { useState, useEffect } from 'react';
import { DataTable } from '@/components/ui/DataTable';
import { User, getUsers, deleteUser } from '@/services/userService';
import Sidebar from '@/components/admin/sidebar'; // Asumo que tienes un componente Sidebar

export default function DashboardLayoutAdmin() {
  const [users, setUsers] = useState<User[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  // Fetch users al cargar el componente
  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const data = await getUsers();
        setUsers(data);
      } catch (error) {
        console.error('Error fetching users:', error);
      } finally {
        setIsLoading(false);
      }
    };
    fetchUsers();
  }, []);

  // Handler para eliminar usuario
  const handleDelete = async (userId: string) => {
    if (confirm('¿Estás seguro de eliminar este usuario?')) {
      await deleteUser(userId);
      setUsers(users.filter((user) => user._id !== userId));
    }
  };

  // Columnas de la tabla
  const userColumns = [
    { key: 'name', header: 'Nombre', width: 'w-1/4' },
    { key: 'email', header: 'Email', width: 'w-1/4' },
    { key: 'role', header: 'Rol', width: 'w-1/4' },
    { 
      key: 'isActive', 
      header: 'Estado', 
      width: 'w-1/4',
      render: (value: boolean) => (
        <span className={`px-2 py-1 rounded-full text-xs ${
          value ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
        }`}>
          {value ? 'Activo' : 'Inactivo'}
        </span>
      )
    },
  ];

  return (
    <div className="flex flex-row min-h-screen">
      {/* Sidebar */}
      <Sidebar />

      {/* Contenido principal */}
      <div className="flex-1 p-8 bg-gray-50">
        <div className="mb-6 flex justify-between items-center">
          <h1 className="text-2xl font-bold text-gray-800">Gestión de Usuarios</h1>
          <button className="bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700">
            + Nuevo Usuario
          </button>
        </div>

        {/* DataTable */}
        <div className="bg-white rounded-lg shadow overflow-hidden">
          <DataTable
            columns={userColumns}
            data={users}
            onEdit={(id) => console.log('Editar:', id)} // Redirigir a /admin/users/edit/[id]
            onDelete={handleDelete}
            isLoading={isLoading}
          />
        </div>
      </div>
    </div>
  );
}